.livescore-container {
  position: absolute;
  top: 43vh;
  right: 50px;
  width: 30%;
  height: 39vh;
  background-color: #f9f9f9;
  border: 2px solid #ddd;
  border-radius: 10px;
  overflow-y: auto;
  padding: 4px 20px;
  z-index: 1;
}
.custom-h3 {
  color: #187313;
  font-family: 'Fredoka One', cursive;
  font-size: 25px;
  margin-top: 0px;
}
table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 10px;
  text-align: center;
}

th {
  background-image: url('../Image/PlayZoneBackground3.jpg');
  border-bottom: 2px solid #3b3636;
  color: #f9f9f9;
  font-family: 'Fredoka One', cursive;
  font-size: 20px;
}

td {
  background-color: #fff;
  border-bottom: 1px solid #10460c;
  font-weight: bold;
  font-size: 15px;
}
