.scoreboard-container {
  position: fixed;
  top: cal(30px+30vh);
  right: 50px;
  width: 30%;
  height: 36vh;
  background-color: #f9f9f9;
  border: 2px solid #ddd;
  border-radius: 10px;
  overflow-y: auto;
  padding: 20px;
  z-index: 1;
}
.custom-h3 {
  color: #187313;
  font-family: 'Fredoka One', cursive;
  font-size: 25px;
  margin-top: 0px;
}
table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 15px;
  text-align: center;
}

th {
  background-image: url('../Image/PlayZoneBackground3.jpg');
  border-bottom: 2px solid #3b3636;
  color: #f9f9f9;
  font-family: 'Fredoka One', cursive;
  font-size: 20px;
}

td {
  background-color: #fff;
  border-bottom: 1px solid #10460c;
  font-weight: bold;
  font-size: 25px;
}

tr:last-child td {
  border-bottom: none;
}

tr:nth-child(even) {
  background-color: #f5f5f5;
}

tr:hover {
  background-color: #e0e0e0;
}

tr:first-child {
  font-weight: bold;
}

td:first-child {
  font-weight: bold;
}
