.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  width: 80%;
  max-width: 500px;
  color: rgb(10, 127, 18);
}

.modal-content h1 {
  color: rgb(10, 127, 18);
}

.modal-content h3 {
  color: rgb(10, 127, 18);
}

.modal-content button {
  background-color: rgb(10, 127, 18);
  color: white;
  padding: 10px 50px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin: 10px;
}

.modal-content button:hover {
  background-color: rgb(165, 194, 167);
}

.emoji {
  font-size: 80px;
  margin-bottom: 20px;
  animation: bounce 1s infinite alternate;
}

@keyframes bounce {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10px);
  }
}

.score-card {
  display: flex;
  justify-content: space-between;
}

.score-score {
  display: flex;
  justify-content: space-between;
}

.celebration-emoji {
  animation: celebration-animation 2s ease-in-out infinite;
}

.sad-emoji {
  animation: sad-animation 2s ease-in-out infinite;
}

@keyframes celebration-animation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes sad-animation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
